.main_div {
    background: #6891cf;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.center_div {
    box-shadow: 0px 3px 20px #00000099;
    border-radius: 12px;
    background: #fff;
    padding: 20px;
}

.center_div input[type="text"] {
    border: 0;
    border-bottom: 3px solid #911ee8;
    padding: 0px 0 5px;
    text-align: center;
}

.center_div button {
    background: #911ee8;
    border: 0;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    color: #fff;
    margin-left: 17px;
}
.center_div ul li{
    list-style-type: none;
}